*,*::before,*::after{box-sizing:border-box;margin:0;padding:0}h1,h2,h3,h4,h5,h6,p{margin:0;padding:0}a{text-decoration:none}ul{padding:0;margin:0;list-style:none}body{font-family:"Poppins",sans-serif;overflow-x:hidden}.container{margin-left:auto;margin-right:auto;padding-left:15px;padding-right:15px;width:100vw}@media screen and (min-width: 480px){.container{width:480px}}@media screen and (min-width: 768px){.container{width:768px}}@media screen and (min-width: 1200px){.container{width:1200px}}.header{background-image:url("../src/images/Bitmap.jpg");background-size:100vw auto;background-repeat:no-repeat}.nav{display:flex;justify-content:space-between;padding:24px 0 24px 0}.header-menu{background-color:#fff;border-radius:50%}.logo{display:flex;align-items:center;font-size:30px;font-weight:700;color:#fff}@media screen and (min-width: 1200px){.header{min-height:811px}}.banner{color:#fff;padding-bottom:211px}.banner-subheading{font-size:14px;font-weight:400;padding-bottom:22px;padding-top:25px}.banner-title{font-size:25px;font-weight:700}.banner-description{font-size:12px;font-weight:400;padding-bottom:25px}.banner-link{font-size:16px;font-weight:700;color:#ffd723;display:flex;align-items:center}.banner-link>svg{fill:#ffd723;margin-right:10px}@media screen and (min-width: 768px){.banner-title{font-size:40px;font-weight:700;padding-bottom:45px}.banner-description{font-size:16px}.banner-subheading{padding-top:35px}}@media screen and (min-width: 1200px){.banner-title{font-size:60px}.banner-subheading{padding-top:65px}}.client-logos{width:90%;border-radius:5px;background:#fff;box-shadow:50px 50px 100px 0px rgba(0,0,0,.15);position:absolute;top:-50;right:80;padding-top:25px;padding-bottom:25px}main{position:relative}.logos-list{display:flex;justify-content:space-evenly}